@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300;600&display=swap');

body {
    font-family: 'JetBrains Mono', monospace;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.input:focus {
    outline: none;
    border:none;
}

.wrapperAll {
    display: flex;
}

.wrapper {
    width: 50%;
    height: 100%;
    padding: 0px 20px;
    margin: 20px 20px 20px 20px;
    border-radius: 10px;
    border: 1px solid #B7CBFF;
    background-color: #fff;

}

header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
}

header .nameResume input {
    width: 200px;
    font-size: 20px;
    margin: 20px 30px 20px 0;
    border: none;
    background-color: #fff;
    padding: 10px 20px;


}

header .nameResume input:active {
    border: none;
}

header .redactName {
    color: #002fdc;
    font-size: 20px;
    cursor: pointer;
}

main {
    margin-top: 35px;
}

main .upInfo {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

main .upInfo .titleProfession {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

main .upInfo .titleProfession input {
    width: 315px;
    height: 37px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #E2EAFF;

}

.titleProfession h1 {
    font-size: 16px;
    margin-bottom: 10px;
}

main .photoAndName {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    align-items: flex-start;
}

main .photoAndName .photo {
    width: 220px;
    height: 254px;
    background-color: #dadada;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow: hidden;
    position: relative;
}

.imgAvInp.active {
    display: inline-block;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 16px;
    vertical-align: middle;
    color: #000;
    text-align: center;
    border-radius: 4px;
    background-color: #dadada;
    line-height: 22px;
    bottom: 0px;
    right: 3%;
    width: 90%;
    position: absolute;
    height: 40px;
    padding: 10px 20px;
    box-sizing: border-box;
    border: none;
    margin: 0;
    transition: background-color 0.2s;
}

.input-file.active {
    display: inline-block;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 16px;
    vertical-align: middle;
    color: #000;
    text-align: center;
    border-radius: 4px;
    background-color: #dadada;
    line-height: 22px;
    bottom: 0;
    right: 3%;
    width: 90%;
    position: absolute;
    height: 40px;
    padding: 10px 20px;
    box-sizing: border-box;
    border: none;
    margin: 0;
    transition: background-color 0.2s;
}

.input-file {
    display: inline-block;
}
.input-file-btn {
    display: inline-block;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 16px;
    vertical-align: middle;
    color: #000;
    text-align: center;
    border-radius: 20px;
    background-color: #dadada;
    line-height: 22px;
    height: 40px;
    padding: 10px 20px;
    box-sizing: border-box;
    border: none;
    margin: 0;
    bottom: 40%;
    right: 10%;
    z-index: 1;
    position: absolute;
    transition: background-color 0.2s;
}

.input-file-btn.active {
    right: 16%;
}

.imagePDF img {
    max-width: 100%;
    padding: 20px;
    border-radius: 35px;
}

.input-file input[type=file] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    display: block;
    width: 0;
    height: 0;
}

.imgAvatr {
    max-width: 100%;
    height: auto;
    position: absolute;
}

main .photoAndName .nameAll .OneNameCandidate, .TwoNameCandidate, .ThreeNameCandidate{
    margin: 0 0 20px 10px;
}

