}

.iconInput {
    position: relative;
}


.chooseIcon {
    width: 10px;
    height: 10px;
}

.btnChooseYear {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    right: 0px;
    background-color: #cecece;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    text-align: center;
    color: #fff;
    cursor: pointer;
}

.btnChooseMonth {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    right: 0px;
    background-color: #cecece;
    width: 20px;
    height: 20px;
    border-radius: 50%;
