}

.flex_birthday {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.allBlockBirth {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 10px;
}

.allDayBirthday {
    top: 50px;
    position: absolute;
    height: 150px;
    background-color: #fff;
    width: 100%;
    display: flex;
    overflow: scroll;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    border: 1px solid #ccc;
}

.allMonthBirthday {
    position: absolute;
    background-color: #fff;
    height: 150px;
    width: 100%;
