    overflow: scroll;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    border: 1px solid #ccc;
}

.btnChooseDay.active {
    transform: rotateX(180deg);
    transition: .5s all ease;

}

.day {
    cursor: pointer;
    width: 100%;
    padding: 10px 20px;
    text-align: center;
    background-color: #e0e0e0;
    border-bottom:  1px solid #ccc;
    cursor: pointer;
}

.dayB {
    padding: 10px 10px;
